.introduction-dialog {
  position: fixed;
  top: 64px;
  right: 10px;
  z-index: 4;
  width: 350px;
  height: 187px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(6px);
}
.x-button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 32px;
  height: 32px;
}
.x-button-img {
  width: 32px;
  height: 32px;
}
.introduction {
  color: #000;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%; /* 24px */
  margin: 42px 15px 0px;
}
.email {
  position: absolute;
  display: flex;
  bottom: 18px;
  right: 18px;
  width: 242px;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 150px;
  background: #000;
  color: #fff;
  text-align: right;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 185%; /* 29.6px */
}
