@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}

#teams {
  opacity: 0;
  position: relative;
  animation: fade-in 3s ease 1s forwards;
}

#banner-small {
  color: white;
  position: absolute;
  top: 0px;
  left: 10px;
  z-index: 99;
  padding: 15px;
  font-size: 45px;
  font-weight: 600;
}
#banner-small > span,
#banner > .p1 > span {
  font-size: 12px;
}
#banner {
  left: 45px;
  bottom: 15px;
  width: 550px;
  color: white;
  position: absolute;
  z-index: 99;
}
#banner > .p1 {
  margin-block-end: 0;
  width: 600px;
  font-weight: 600;
  font-size: 36px;
}
#banner > .p2 {
  width: 750px;
  font-size: 18px;
  font-weight: 400;
}
