.m-top-bar {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  vertical-align: middle;
  z-index: 4;
  width: 100vw;
  height: 55px;
  padding: 0px 20px 0px 20px;
}

.team-name {
  display: flex;
  width: 125px;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  vertical-align: middle;
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.m-contact-us {
  background-color: white;
  display: flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  text-align: center;
  border-radius: 10px;
  width: 136px;
  height: 36px;
  color: #000;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: auto 0;
}

.d-top-bar {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  vertical-align: middle;
  z-index: 4;
  width: 100vw;
  height: 55px;
  padding: 0px 20px 0px 20px;
}

.d-center-title {
  color: black;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: auto 0;
}

.d-contact-us {
  background-color: black;
  display: flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  text-align: center;
  border-radius: 18px;
