.donation-page {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding: 55px 20px 20px 20px;
}
.canvas-container {
  position: relative;
  width: 100%;
  height: calc(100% - 120px);
  border-radius: 20px;
  overflow: hidden;
}
.text-area {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-starxt;
  padding: 24px;
  gap: 20px;
}
.m-main-title {
  text-align: left;
  color: #000;
  font-family: Inter;
  font-size: 64px;
  font-style: normal;
  font-weight: 700;
  line-height: 98.023%; /* 62.735px */
  text-transform: uppercase;
}
.d-main-title {
  margin-top: 96px;
  margin-left: 6px;
  width: 532px;
  text-align: left;
  color: #000;
  font-family: Inter;
  font-size: 110px;
  font-style: normal;
  font-weight: 700;
  line-height: 98.023%; /* 62.735px */
  text-transform: uppercase;
}

.m-context {
  color: #000;
  text-align: right;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 16px */
}
.d-context {
  width: 430px;
  color: #000;
  text-align: right;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 16px */
}

.m-bank-account {
  position: absolute;
  left: 22px;
  bottom: 22px;
  z-index: 4;
  color: #fff;
  text-align: left;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 98.023%; /* 15.684px */
  text-transform: uppercase;
}
.d-bank-account {
