.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.scrollSnapWrap {
  scroll-snap-type: y mandatory;
  height: 100vh;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.container {
  display: grid;
  scroll-snap-align: start;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#sudo-scroller {
  width: 100px;
  height: 100px;
  z-index: 999;
  position: fixed;
  bottom: 40px;
  left: calc(50% - 50px);
  text-align: center;
  line-height: 60px;
  color: white;
  text-shadow: 1px 0px 5px black, 0px 1px 5px black, -1px 0px 5px black, 0px -1px 5px black;
  opacity: 0;
  animation: fade-in 3s ease 1s forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}